import React from "react";
import "./UserProgressCircle.scss";
import { parseNumber } from "../../../utils";

const UserProgressCircle = ({progress, children }) => {
  return (
    <div className="progress-wrapper">
      <div className="progress-circle">
        {children}
        <svg className="progress-ring" viewBox="0 0 120 120">
          <circle className="progress-bg" cx="60" cy="60" r="54" />
          <circle
            className="progress-bar"
            cx="60"
            cy="60"
            r="54"
            style={{
              strokeDashoffset: parseNumber(339.292 - (progress / 100) * 339.292),
            }}
          />
        </svg>
      </div>
      <div className="progress-label">{progress}%</div>
    </div>
  );
};

export default UserProgressCircle;
